import "core-js/modules/es.array.push.js";
import { reactive, toRefs } from '@vue/reactivity';
import { getserch } from "../assets/api/home";
import { Toast } from 'vant';
import 'vant/es/toast/style';
import router from '@/router';
import { useRouter } from 'vue-router';
export default {
  setup() {
    //实现响应式数据
    let obj = reactive({
      serachData: [],
      value: ""
    });
    let routers = useRouter();

    // 搜索框的数据绑定
    let getValue = () => {
      //如果输入框内容为空
      if (obj.value == "") {
        Toast("请输入内容");
      } else {
        //否则请求数据
        getserch(obj.value).then(data => {
          obj.serachData = data.data.results.list;
          console.log(obj.serachData);
        });
      }
    };

    //清空输入框的内容
    let clearText = () => {
      obj.value = "";
    };

    // 取消
    let cancel = () => {
      router.go(-1);
    };

    // 进入购买页面
    let goShop = id => {
      console.log(id);
      routers.push({
        path: "/shopping",
        query: {
          key: id
        }
      });
    };
    return {
      ...toRefs(obj),
      getValue,
      clearText,
      cancel,
      goShop
    };
  }
};